import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the Page5Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-page5',
  templateUrl: 'page5.html',
})
export class Page5Page {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Page5Page');
  }

  ionViewWillEnter() {
    console.log('ionViewWillEnter Page5Page');
    console.log('after', this.navCtrl.getViews());
  }

  goPage() {
    // this.navCtrl.popTo(this.navCtrl.getByIndex(2));
    this.navCtrl.popToRoot();
  }
}



